import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
const InputMsisdn = lazy(() => import("../partials/InputMsisdn"));

export class SignIn extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isLoading: false,
			msisdn: "",
			msisdnError: false,
		};
	}
	componentDidMount() {
		window.addEventListener("keydown", this.handleKeydown);
	}

	componentWillUnmount() {
		window.removeEventListener("keydown", this.handleKeydown);
	}

	handleKeydown = (event) => {
		console.log(event);
	};

	handleMsisdn = (status, msisdn) => {
		this.setState({
			msisdnError: status,
			msisdn: msisdn,
		});
	};

	onSubmitToLogin = (e) => {
		e.preventDefault();
		console.log("submitted");
		console.log(this.state.msisdn);
	};

	render() {
		return (
			<div style={{ textAlign: "center" }}>
				<h2 style={{ fontWeight: "bold" }}>Sign In Screen</h2>
				<form onSubmit={this.onSubmitToLogin}>
					<div className="form-row">
						<Suspense fallback={<div></div>}>
							<InputMsisdn
								onChange={this.handleMsisdn}
								isRequired={true}
								label="Please Enter Jazz Number"
								placeholder="03xx-xxxxxxx"
								isLabel={true}
								defaultValue=""
							/>
						</Suspense>
					</div>
					<button type="submit" className="btn-red">
						Login
					</button>
				</form>
			</div>
		);
	}
}

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps)(SignIn);
